import Vue from 'vue'
import Router from 'vue-router'
import collage_goods from '../components/collage/collage_goods'
import commodity_details from '../components/CommodityDetails/CommodityDetails'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'collage_goods',
        component: collage_goods
    }, {
        path: '/commodity_details/:info',
        name: 'commodity_details',
        component: commodity_details
    }]
})


