<template>
  <div class="CommodityDetailsContent">
    <img src="../../../static/images/img-2@2x.png" alt="" srcset="">
    <div class="RemainingTime">
      <span class="fl">拼团</span>
      <span class="fr">07天06时54分30秒</span>
      <span class="fr">剩余时间：</span>
    </div>
    <div class="goodsInfo">
      <p>乙烯利芒果，4人团5斤仅需39</p>
      <p>
        <span class="GroupPurchase">四人团</span>
        <span class="PresentPrice">
          <span>￥</span>
          <span>39</span>
        </span>
        <span>单买价:</span>
        <span>￥59</span>
      </p>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {

      }
    },
    methods:{

    },
    created(){

    },
    watch:{

    },
    components:{

    }
  }
</script>

<style scoped lang="scss">
  @import '../../../static/scss.scss'; //导入外部scss文件 
  .CommodityDetailsContent{
    font-family: PingFang-SC-Medium;
    img{
      width: 100%;
      display: block;
      height: pxToRem(450);
    }
    .RemainingTime{
      height: pxToRem(80);
      line-height: pxToRem(80);
      padding-left: pxToRem(36);
      padding-right: pxToRem(20);
      color: #ffffff;
      background-color: #f87a7c;
      .fl{
        font-size: pxToRem(30);
      }
      .fr{
        font-size: pxToRem(24);
      }
    }
    .goodsInfo{
      padding-top: pxToRem(30);
      padding-left: pxToRem(36);
      p:first-child{
        font-size: pxToRem(30);
        color: #333;
        margin-bottom: pxToRem(20);
      }
      p:last-child{
        .GroupPurchase{
          display: inline-block;
          text-align: center;
          width: pxToRem(68);
          height: pxToRem(30);
          line-height: pxToRem(30);
          background-color: #f87a7c;
          border-radius: pxToRem(4);
          color: #fff;
          font-size: pxToRem(20);
         margin-right: pxToRem(10);
        }
        .PresentPrice{
          color: #f87a7c;
          margin-right: pxToRem(20);
          span:first-child{
            font-size: pxToRem(24);
          }
          span:last-child{
            font-size: pxToRem(36);
          }
        }
        span:nth-child(3){
          font-size: pxToRem(24);
          color: #666;
        }
        span:nth-child(4){
          font-size: pxToRem(24);
          color: #666;
        }
      }
    }
  }
</style>



